
body {
      font-family: "PingFang SC", "Hiragino Sans GB", "Helvetica Neue", Roboto, Noto, sans-serif;
      font-size: 12px;
      -webkit-font-smoothing: antialiased;
      text-justify: 100%;
}
* {
      margin: 0;
      padding: 0;
      outline: 0;
      user-select: none;
      -webkit-tap-highlight-color: transparent;
}
html.lock {
      overflow: hidden;
      touch-action: none;
}
.lock body {
      overflow: hidden;
      touch-action: none;
}
#app {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
}
#app.showCart .menu-wrap {
      padding-bottom: 54px;
}
.piccard-enter-active,
.piccard-leave-active {
      transition: all 0.3s ease;
      will-change: transform, opacity;
      transform: scale(1);
      opacity: 1;
}
.piccard-leave-active,
.piccard-enter {
      transform: scale(0.8);
      opacity: 0;
}



